@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-citas{
    list-style: none;
    padding: 0;

    li{
        padding: 1rem;
        border-top: 1px solid v.$blanco;    
        p{
            font-weight: v.$bold;
            color: v.$azul;
            
            span, .servicios{
             font-weight: v.$regular;
             color: v.$blanco;
            }
       }
    }
    
}

.total{
    font-weight: v.$bold;
    color: v.$azul;
    
    span, .servicios{
     font-weight: v.$regular;
     color: v.$blanco;
    }
}

.cerrar-session{
    display: flex;
    align-items: center;
    justify-content: space-between;
    p{
        span{
            font-weight: v.$bold;
        }
    }
}

.no-citas{
    text-align: center;
    margin-top: 4rem;
}

.barra-servicios{
    display: flex;
    flex-direction: column;

    .boton{
        margin: 0 0 2rem 0;
    }
    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
    }
}

.servicios-admin{
    border-top: 1px solid v.$blanco; 
    p {
        color: v.$blanco;
    }
    span {
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}

.acciones{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    @include m.tablet {
        flex-direction: row;
    }
    input, a{
        margin: 0;
    }
}