@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 5rem;
    width: 100%;
}

.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 15rem;
    }
    input{
        flex: 1; //toma el resto del contenido disponible
        border-radius: 5px;
        border: none;
        padding: calc(v.$separacion / 4);

        &:disabled{
            color: v.$negro;
            background-color: v.$blanco;
            cursor:not-allowed;
        }
    }
}