@use 'base/mixins' as m;
@use 'base/variables' as v;

.acciones{
    
    a{
        color: v.$blanco;
        display: block;
        text-align: center;
        margin-bottom: calc(v.$separacion/2);

        @include m.tablet{
            &:hover {
                color: darken( v.$blanco , 50);
            }
        }
    }

    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }

    &:hover {
        background-color: blacks;
    }
}