@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
    @include m.tablet{
        @include m.grid( 2, 2rem );
        min-height: 100vh;
    }
}

.imagen{
    background-image: url(../img/1.jpg);
    background-size: cover;
    background-position: center center;
    height: 20rem;

    @include m.tablet{
        height: auto;
    }
}

.app{
    width: 95%;
    margin: 0 auto;
    padding: 2rem 0;

    @include m.tablet{
        overflow: scroll;
        overflow-x: hidden;
    }

    h2{
        margin: 2rem 0 2rem 0;
    }
}

