@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{
    display: grid;
    gap: 2rem;
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    margin-bottom: 2rem;


    .servicio-contenedor{
        background-color: v.$blanco;
        border-radius: 5px;
        text-align: center;
        padding: 1.2rem;

        transition-property: transform; //indicamos la transicion 
        transition-duration: 300ms;

        &.seleccionado{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }

        &:hover {
            transform: scale(1.05);
            cursor: pointer;
        }

        p{
            margin: 0;
            margin: 0 0 calc(v.$separacion / 4);
        }

        .nombre-servicio{
            color: v.$negro;
        }

        .precio-servicio{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 2.5rem;
            margin: 0;
        }
    }

}