@use 'base/variables' as v;

.alerta{
    padding: calc(v.$separacion /4);
    display: block;
    width: 100%;
    margin-bottom: .8rem;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;

    &.error{
        background-color: v.$rojo;
    }

    &.exito{
        background-color: v.$verde;
    }
}